import { parseChainId } from '../../utils';
import { NETWORKS_BY_CHAIN_ID } from '../contract/SampleContracts';

export const OKX_DEX_ROUTER = {
  'mainnet': '0x7d0ccaa3fac1e5a943c5168b6ced828691b46b36',
  'optimism': '0xf332761c673b59b21ff6dfa8ada44d78c12def09',
  'bsc': '0x9333c74bdd1e118634fe5664aca7a9710b108bab',
  'polygon': '0xa748d6573aca135af68f2635be60cb80278bd855',
  'base': '0x6b2c0c7be2048daa9b5527982c29f48062b34d58',
  'arbitrum': '0xf332761c673b59b21ff6dfa8ada44d78c12def09',
  'avalanche': '0x1dac23e41fc8ce857e86fd8c1ae5b6121c67d96d',
};

const supportNetworkNames = Object.keys(OKX_DEX_ROUTER);

export default {
  sendTransaction: (from: string, to: string, chainId: string | undefined) => {
    const network =
      NETWORKS_BY_CHAIN_ID[parseChainId(chainId) as keyof typeof NETWORKS_BY_CHAIN_ID];
    if (!OKX_DEX_ROUTER[network as keyof typeof OKX_DEX_ROUTER]) {
      return [
        {
          'id': 'sendTransaction-okx-not-support',
          'name': 'OKX DEX 不支持',
          'description': 'OKX DEX 不支持',
          'value': `当前网络不支持，支持的网络为：${supportNetworkNames.join(',')}`,
        },
      ];
    }

    const contractAddress = OKX_DEX_ROUTER[network as keyof typeof OKX_DEX_ROUTER];

    return [
      {
        'id': 'okx-dex-uniswapV3SwapTo_0x0d5f0e3b',
        'name': 'OKX DEX uniswapV3SwapTo',
        'description': '测试 OKX DEX uniswapV3SwapTo',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          data: '',
        }),
      },
      {
        'id': 'okx-uniswapV3SwapTo_0x08298b5a',
        'name': 'OKX uniswapV3SwapTo',
        'description': '测试 OKX uniswapV3SwapTo',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          data: '',
        }),
      },
      {
        'id': 'okx-unxswapByOrderId_0x9871efa4',
        'name': 'OKX unxswapByOrderId',
        'description': '测试 OKX unxswapByOrderId',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          value: '0x2c68af0bb140000', // 0.2 ETH
          data: '',
        }),
      },
      {
        'id': 'okx-smartSwapByOrderId_0xb80c2f09',
        'name': 'OKX smartSwapByOrderId',
        'description': '测试 OKX smartSwapByOrderId',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          value: '0x2c68af0bb140000', // 0.2 ETH
          data: '',
        }),
      },
      {
        'id': 'okx-smartSwapTo_0x03b87e5f',
        'name': 'OKX smartSwapTo',
        'description': '测试 OKX smartSwapTo',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          data: '',
        }),
      }
    ];
  },
};
