/* eslint-disable @typescript-eslint/no-unsafe-assignment */

import { parseChainId } from '../../utils';
import { NETWORKS_BY_CHAIN_ID } from '../contract/SampleContracts';

export const INCH_AGGREGATION_ROUTER_V5 = {
  'mainnet': '0x1111111254eeb25477b68fb85ed929f73a960582',
  'optimism': '0x1111111254eeb25477b68fb85ed929f73a960582',
  'bsc': '0x1111111254eeb25477b68fb85ed929f73a960582',
  'polygon': '0x1111111254eeb25477b68fb85ed929f73a960582',
  'base': '0x1111111254eeb25477b68fb85ed929f73a960582',
  'arbitrum': '0x1111111254eeb25477b68fb85ed929f73a960582',
  'avalanche': '0x1111111254eeb25477b68fb85ed929f73a960582',
};

const supportNetworkNames = Object.keys(INCH_AGGREGATION_ROUTER_V5);

export default {
  sendTransaction: (from: string, to: string, chainId: string | undefined) => {
    const network =
      NETWORKS_BY_CHAIN_ID[parseChainId(chainId) as keyof typeof NETWORKS_BY_CHAIN_ID];
    if (!INCH_AGGREGATION_ROUTER_V5[network as keyof typeof INCH_AGGREGATION_ROUTER_V5]) {
      return [
        {
          'id': 'sendTransaction-inchV5-not-support',
          'name': 'InchV5 不支持',
          'description': 'InchV5 不支持',
          'value': `当前网络不支持，支持的网络为：${supportNetworkNames.join(',')}`,
        },
      ];
    }

    const contractAddress =
      INCH_AGGREGATION_ROUTER_V5[network as keyof typeof INCH_AGGREGATION_ROUTER_V5];

    return [
      {
        'id': 'inchV5-swap_0x12aa3caf',
        'name': 'InchV5 swap',
        'description': '测试 InchV5 swap',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          data: '',
        }),
      },
      {
        'id': 'inchV5-swap_0x07ed2379',
        'name': 'InchV5 ETH --> DEXE',
        'description': '测试 InchV5 redeem',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          data: '',
        }),
      },
      {
        'id': 'inchV5-uniswapV3Swap_0xe449022e',
        'name': 'InchV5 uniswapV3Swap',
        'description': '测试 InchV5 uniswapV3Swap',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          data: '',
        }),
      },
      {
        'id': 'inchV5-uniswapV3SwapTo_0xbc80f1a8',
        'name': 'InchV5 uniswapV3SwapTo',
        'description': '测试 InchV5 uniswapV3SwapTo',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          data: '',
        }),
      },
      {
        'id': 'inchV5-uniswapV3SwapToWithPermit_0x2521b930',
        'name': 'InchV5 uniswapV3SwapToWithPermit',
        'description': '测试 InchV5 uniswapV3SwapToWithPermit',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          data: '',
        }),
      },
      {
        'id': 'inchV5-unoswap_0x0502b1c5',
        'name': 'InchV5 unoswap',
        'description': '测试 InchV5 unoswap',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          value: '0x2c68af0bb140000', // 0.2 ETH
          data: '',
        }),
      },
      {
        'id': 'inchV5-unoswapTo_0xf78dc253',
        'name': 'InchV5 unoswapTo',
        'description': '测试 InchV5 unoswapTo',
        'value': JSON.stringify({
          from,
          to: contractAddress,
          value: '0x2c68af0bb140000', // 0.2 ETH
          data: '',
        }),
      },
    ];
  },
};

